
import { defineComponent, SetupContext } from 'vue';

type Props = {
  calKey: string;
};

export default defineComponent({
  name: 'CalBtn',
  props: {
    calKey: {
      type: String,
      default: '0',
      required: true
    }
  },
  setup(props: Props, context: SetupContext) {
    const pushBtn = (): void => {
      context.emit('push-btn', props.calKey);
    };

    return { pushBtn };
  }
});
