
import { defineComponent } from 'vue';

type Props = {
  calDisplay: string;
};

export default defineComponent({
  name: 'CalDisplay',
  props: {
    calDisplay: {
      type: String,
      default: '0',
      required: true
    }
  },
  setup() {
    const wrapText = (num: string): string => {
      return num.length < 11 ? num : `${num.slice(0, 11)}...`;
    };
    return { wrapText };
  }
});
